import {formatTimeToStr, formatNera} from "@/plugins/utils";

export default {
  props: {
    dataInfo: {
      type: Object,
      default: () => {
      },
    }
  },
  data() {
    return {
      txtColor: '#6A47FE',
      title: 'Message15',
      icon: '',
    }
  },
  computed: {
    time() {
      return formatTimeToStr(this.dataInfo.iCreateTime)
    }
  },
  methods: {
    getShowValue() {
      this.title = this.calcTitle();
      if (this.dataInfo.iType == 2) {
        this.txtColor = '#FF2424';
        this.icon = require('@/assets/message/finance1.png')
        return '+' + parseFloat(this.dataInfo.dAmount);
      } else {
        this.txtColor = '#6A47FE';
        this.icon = require('@/assets/message/finance2.png')
        return '-' + parseFloat(this.dataInfo.dAmount);
      }
    },
    calcTitle() {
      switch (this.dataInfo.iStatus) {
        case 2:
        case 3:
          return 'Mine34'
        case 4:
          return 'Mine35'
        case 5:
          return 'Mine36'
        case 6:
          return 'Mine37'
        case 7:
          return 'Mine38'
        case 8:
          return 'Mine39'
        case 9:
          return 'Mine40'

        case 11:
        case 12:
          return 'Mine41'
        case 13:
          return 'Message15'
        case 14:
          return 'Mine42'
      }
    },
  }
};
