import {formatTimeToStr, formatNera} from "@/plugins/utils";

export default {
  props: {
    dataInfo: {
      type: Object,
      default: () => {
      },
    }
  },
  data() {
    return {
      txtColor: '#6A47FE',
      title: 'Message15',
      icon: '',
      signDesc: '',
    }
  },
  computed: {
    time() {
      let now = new Date();
      let date = new Date(this.dataInfo.iCreateTime);
      if(now.getMonth() === date.getMonth() && now.getDate() === date.getDate()){
        return date.getHours() + ':' + ((date.getMinutes() + 101) + '').substr(1) + ':' + ((date.getSeconds() + 101) + '').substr(1);
      }
      else{
        return date.getFullYear() + '-' + ((date.getMonth() + 101) + '').substr(1) + '-' + ((date.getDate() + 101) + '').substr(1);
      }
    }
  },
  methods: {
    getShowValue() {
      switch (this.dataInfo.iBusiType) {
        case 1:
          this.title = 'Message10'
          this.icon = require('@/assets/message/sign2.png')
          break;
        case 2:
          this.title = 'Message09'
          this.icon = require('@/assets/message/sign3.png')
          break;
        case 3:
          this.title = 'Message11'
          this.icon = require('@/assets/message/sign10.png')
          break;
        case 4:
          this.title = 'Message12'
          this.icon = require('@/assets/message/sign5.png')
          break;
        case 5:
          this.title = 'Message13'
          this.icon = require('@/assets/message/sign6.png')
          break;
        case 6:
          this.title = 'Message14'
          this.icon = require('@/assets/message/sign7.png')
          break;
        case 7:
          this.title = 'Message17'
          this.icon = require('@/assets/message/sign8.png')
          break;
        case 8:
          this.title = 'Message18'
          this.icon = require('@/assets/message/sign9.png')
          break;
        case 9:
          this.title = 'Message15'
          this.icon = require('@/assets/message/finance1.png')
          break;
        case 10:
          this.title = 'Message16'
          this.icon = require('@/assets/message/finance2.png')
          break;
        case 11:
          this.title = 'Message07'
          this.icon = require('@/assets/message/sign1.png')
          break;
        case 12:
          this.title = 'Message08'
          this.icon = require('@/assets/message/sign4.png')
          break;
        default:
          break;
      }
      if (parseFloat(this.dataInfo.iAvailableChange)) {
        let value = parseFloat(this.dataInfo.iAvailableChange);
        this.txtColor = '#FF2424';
        let sign = '+'
        if (this.dataInfo.iBusiType === 12) {
          sign = ''
          this.signDesc = 'Message22'
        }
        if (this.dataInfo.iBusiType === 11) {
          sign = ''
          this.signDesc = 'Message23'
          value = Math.abs(value);
        }
        return sign + value;
      }
      if (parseFloat(this.dataInfo.iFrozenChange)) {
        let value = parseFloat(this.dataInfo.iFrozenChange);
        this.txtColor = '#6A47FE';
        let sign = ''
        if (this.dataInfo.iBusiType === 11) {
          sign = ''
          this.signDesc = 'Message23'
          value = Math.abs(value);
        }
        if (this.dataInfo.iBusiType === 12) {
          sign = ''
          this.signDesc = 'Message22'
        }
        return sign + value;
      }
      return '';
    },
  }
};
