import CapitalItem from "./components/capital-item.vue";
import FinanceItem from "./components/finance-item.vue";

export default {
  layout: "single-container",
  name: "nera",
  components: {CapitalItem, FinanceItem},
  data() {
    return {
      address: "",
      vBalance: {},
      loading: false,
      finished: false,
      iPageNo: 0,
      iPageSize: 20,
      list: [],
      active: '',
    };
  },
  watch: {
    active() {
      this.iPageNo = 0;
      this.loading = true;
      this.finished = false;
      this.list = [];
      this.onLoad();
    }
  },
  computed: {},
  mounted() {
    this.getAmountCount();
  },
  methods: {
    async getAmountCount() {
      let {vBalance} = await this.$axios.post("/v1/udc/GetUserBalance", {
        iAcctType: 0,
      });
      this.vBalance = vBalance[0];
    },
    async queryFinanceList() {
      let {vRecords} = await this.$axios.post("/v1/udc/QueryFinanceList", {
        iPageNo: this.iPageNo,
        iPageSize: this.iPageSize,
        iType: 0,
      });
      this.loading = false;
      if (vRecords.length < this.iPageSize) {
        this.finished = true;
      }
      this.list = [...this.list, ...vRecords];
      this.loading = false;
    },
    async getUserJournalList() {
      let {vList} = await this.$axios.post("/v1/udc/GetUserJournalList", {
        iPageNo: this.iPageNo,
        iPageSize: this.iPageSize,
        sToken: "NERA",
        iCreateTime_Begin: 0,
        iCreateTime_End: 0,
        iBusisType: 0,
      });
      this.loading = false;
      if (vList.length < this.iPageSize) {
        this.finished = true;
      }
      this.list = [...this.list, ...vList];
      this.loading = false;
    },
    onLoad() {
      if (this.finished) return;
      if (this.active == '0') {
        this.getUserJournalList();
      } else {
        this.queryFinanceList();
      }
      this.iPageNo++;
    },
  },
};
